.component-tutoriel-gutenberg-custom-toggle-control {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.component-tutoriel-gutenberg-custom-toggle-control__label {
  font-weight: 700;
}
.component-tutoriel-gutenberg-custom-toggle-control__toggle {
  position: relative;
  display: block;
  width: 40px;
  height: 20px;
  border-radius: 50px;
  background-color: white;
  border: 1px solid black;
  cursor: pointer;
}
.component-tutoriel-gutenberg-custom-toggle-control__toggle::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 3px;
  height: 14px;
  width: 14px;
  border-radius: 50px;
  background-color: black;
  transition: 0.3s all;
}
.component-tutoriel-gutenberg-custom-toggle-control__toggle.active::after {
  left: calc(100% - 14px - 3px);
}